@import url('https://fonts.googleapis.com/css2?family=Aldrich&display=swap');

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  font-family: 'Aldrich', sans-serif;
  line-height: 1.5;
}

.app {
  min-height: 100vh;
  padding-left: 20px;
  padding-right: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: hsl(0, 0%, 10%);
}

h2 {
  color: hsla(183, 71%, 36%, 1);
  font-size: 30px;
  text-align: center;
  margin: 0;
}

.timer {
  color: hsl(183, 99%, 70%);
  font-size: 150px;
  margin-bottom: 20px;
}

.buttons button {
  outline: none;
  border: none;
  font-size: 24px;
  border-radius: 15px;
  padding: 15px 30px;
  cursor: pointer;
  -webkit-transition: 0.3s ease all;
  -o-transition: 0.3s ease all;
  transition: 0.3s ease all;
  color: hsl(183, 99%, 70%);
  background: hsl(183, 84%, 22%);
  -webkit-box-shadow: 0 2px 5px hsl(183, 99%, 70%);
          box-shadow: 0 2px 5px hsl(183, 99%, 70%);
  -webkit-transform: translateY(-2px);
      -ms-transform: translateY(-2px);
          transform: translateY(-2px);
}

.buttons button:hover {
  color: hsl(0, 0%, 10%);
  background: hsl(183, 84%, 38%);
  -webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
          transform: translateY(0px);
}

.buttons button:not(:last-child) {
  margin-right: 10px;
}

@media (max-width: 780px) {
  h2 {
    font-size: 22px;
  }

  .timer {
    font-size: 80px;
  }

  .buttons button {
    font-size: 18px;
  }
}



